@import url(https://fonts.googleapis.com/css?family=Lato);
@import url(https://fonts.googleapis.com/css?family=Merriweather);
@import url(https://fonts.adobe.com/fonts/ff-tisa-sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.topping {
  margin-top: 0.3rem;
  vertical-align: text-bottom;
}

.result {
  margin-top: 1rem;
}

.btn-circle.btn-md {
    width: 100px;
    height: 100px;
    padding: 7px 10px;
    border-radius: 50px;
    font-size: 20px;
    text-align: center;
}

.btn-circle.btn-sm {
    width: 80px;
    height: 80px;
    padding: 6px 0px;
    border-radius: 40px;
    font-size: 16px;
    text-align: center;
}

.bg-image{
    background-image: url(/my-app/static/media/wp2748622-paper-wallpaper.f01cde0d.jpg);
    height: 100vh;
}

.bg-color-custom{
    background-color: #f5f5f5;
    height: 100vh;
}

.gradient-custom {
    height: 100vh;
background: rgb(177,177,177);
background: linear-gradient(90deg, rgba(177,177,177,1) 0%, rgba(204,204,204,0.8743872549019608) 50%, rgba(175,175,175,1) 100%);
}

.navbar-light{
        /* fallback for old browsers */
    border-radius: 0px;
    background-color: #e5e5e5;
}

.text-custom{
    font-family: "Lato", sans-serif;
    color: #425352;
}

.text-title{
    font-size: xx-large;
}

.card{
    /*background-color: #eeeeee;*/
    background-color: #ffffff;
    border-radius: 0px;
}

.card-grey{
    background-color: #e2e2e2;
}

.alert{
    border-radius: 0px;
}

.input-group-text{
    border-radius: 0px;
}
